.introduction {
  padding-top: rem(140);
  width: 100%;
  max-width: rem(1280);
  margin-right: auto;
  margin-left: auto; 
  position: relative;
  @include mq(sp) {
    padding-top: rem(120);
    padding-right: rem(15);
    padding-left: rem(15);  
  } 
  &::after {
    content: "";
    width: rem(777.075);
    height: rem(480.675);
    background-image: url(../img/bg1@2x.png);
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: rem(20);
    left: rem(-163);
    z-index: -1;
    @include mq(sp) {
      content: none;
    } 
  }
  &__media {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq(sp) {
      flex-direction: column;
    } 
  }
  &__body {
    margin-right: rem(19);
    @include mq(sp) {
      margin-right: 0;
    } 
  }
  &__title {
    font-size: rem(30);
    font-weight: 800;
    line-height: ( 75 / 45 );
    @include mq(sp) {
      font-size: rem(22);
    }
  }
  &__explain {
    margin-top: rem(35.75);
    font-size: rem(14);
    font-weight: 600;
    line-height: ( 35 / 20 );
    @include mq(sp) {
      margin-top: rem(26);
    } 
  }
  &__image {
    width: 30%;
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }
    @include mq(sp) {
      width: 100%;
      margin-top: rem(25);
    } 
  }
  &__cardUnit {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: rem(26);
    @include mq(sp) {
      flex-direction: column;
    } 
  }
  &__card {
    background-color: #fff;
    &--left {
      margin-right: rem(26);
      color: #00a0e9;
      @include mq(sp) {
        margin-right: 0;
        margin-bottom: rem(20);
      }
    }
    width: rem(430);
    box-shadow: 0 0 rem(10) 0 rgba($color: #00a0e9, $alpha: .2);
    padding: rem(26) rem(40);
    border-radius: rem(30);
    @include mq(sp) {
      width: 100%;
      padding-right: rem(15);
      padding-left: rem(15);
    } 
  }
  &__cardTitle {
    font-size: rem(24);
    font-weight: bold;
    text-align: center;
  }
  &__cardProblem {
    margin-top: rem(19.5);
    font-size: rem(16);
    font-weight: bold;
    text-align: center;
    line-height: rem(40);
    display: block;
    width: 100%;
    position: relative;
    border-radius: rem(20);
    color: #fff;
    &--left {
      background-color: #00a0e9;
      &::after {
        content: "";
        width: rem(20);
        height: rem(20);
        background-color: #00a0e9;
        position: absolute;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
        bottom: rem(-8);
      }
    }
    &--right {
      background-color: #070749;
      &::after {
        content: "";
        width: rem(20);
        height: rem(20);
        background-color: #070749;
        position: absolute;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
        bottom: rem(-8);
      }
    }
  }
  &__problemList {
    margin-top: rem(29);
  }
  &__problemItem {
    font-size: rem(16);
    font-weight: bold;
    position: relative;
    margin-left: rem(20);
    &--left {
      &::before {
        content: "";
        width: rem(16);
        height: rem(16);
        background-color: #00a0e9;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: rem(-20);
        transform: translateY(-50%);
      }
    }
    &--right {
      &::before {
        content: "";
        width: rem(16);
        height: rem(16);
        background-color: #070749;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: rem(-20);
        transform: translateY(-50%);
      }
    }
  }
  &__cardExplain {
    font-size: rem(12);
    font-weight: 600;
    line-height: ( 30 / 18 );
    padding-top: rem(22.75);
    margin-top: rem(22.75);
    &--left {
      border-top: 1px solid #00a0e9;
    }
    &--right {
      border-top: 1px solid #070749;
    }
  }
  &__arrow {
    width: rem(110);
    height: auto;
    margin-right: auto;
    margin-left: auto;
    margin-top: rem(68.25);
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__ok {
    width: 50%;
    height: auto;
    margin-right: auto;
    margin-left: auto;
    margin-top: rem(48.75);
    img {
      width: 100%;
      height: 100%;
    }
    @include mq(sp) {
      width: 100%;
    } 
  }
}